import React from 'react';
import { Route, withRouter } from "react-router-dom";

// routepropsprops
const renderMergedProps = (component, ...rest) => {
    const finalProps = Object.assign({}, ...rest);
    return (
        React.createElement(component, finalProps)
    );
}

// Route -> Routepropsprops
const PropsRoute = ({ component, ...rest }) => {
    
    return (
        <Route {...rest} render={routeProps => {
            console.log('...props of app and Route: ', rest)
            console.log('...props of routeProps: ', routeProps)  // history WithRouterPropsRoute
            return renderMergedProps(component, routeProps, rest);
        }} />
    );
}

// withRoute history
const WithRouterPropsRoute = withRouter(PropsRoute)

export default WithRouterPropsRoute