import eventDispatch from './action'
import { connect } from 'react-redux'

/**
 * action
 * create by jyjin
 * 2019.3.29 05:22
 * @param {} _dispatch 
 * @param ACTION_NAME  action
 * @param data         action
 */
const mapDispatchToProps = _dispatch => {
    return {
        dispatch: (ACTION_NAME, data) => _dispatch(eventDispatch(ACTION_NAME, data))
    }
}

/**
 * mapmapStateToProps
 * create by jyjin
 * 2019.3.29 05:22
 * @param {} map mapStateToPropsmap
 *              {userList: 'user'} => reduceruser.jsstate.userList => this.props.userList
 */
const getMapStateToProps = map => {
    if (!map) {
        return null
    }
    return state => {

        // console.log('store state == ', state)

        // const mapStateToProps = state => {
        //     console.log('state == ', state)
        //     return {
        //         userList: state['user'].userList // stateprops => storestateuserList,  this.props.userList
        //     }
        // };

        // 
        var obj = {}
        for (var key in map) {
            obj[key] = state[map[key]][key]
        }
        // console.log('obj == ', obj)
        return obj
    }
}

/**
 * 
 * connect(
 *      getMapStateToProps({
 *          user: 'userList',
 *          ...
 *      }),
 *      mapDispatchToProps
 * )(Register)
 */



/**
 * map          mapStateToPropsmap
 *              {userList: 'user'} => reduceruser.jsstate.userList => this.props.userList
 * component    
 */
export default (map, component) => {
    return connect(
        getMapStateToProps(map),       // mapStateToProps 
        mapDispatchToProps
    )(component)
}